@import '~node_modules/modern-normalize/modern-normalize.css';
html,
body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  font-family: 'Roboto';
  font-style: normal;
}
h1,
h2,
h3,
p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

button {
  padding: 0;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}
